.Map {
    background-color: #FFFFFF;
    padding-top: 20%;
    padding-bottom: 10%;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.pageTitle {
    font-size: 5vw;
    color: #848484;
    margin-bottom: 5%;
}

.apiButtons {
    font-size: 3vw !important;
    color: #333333 !important;
    font-weight: 400 !important;
}

.transport {
    margin-top: 10%;
    margin-left: 5%;
    width: 90%;
    font-size: 5vw !important;
    color: #000000 !important;
    font-weight: 600 !important;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
}

.transportContent {
    margin-left: 5%;
    width: 90%;
    font-size: 3.5vw;
    line-height: 1.5;
    color: #333333 !important;
    font-weight: 100 !important;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    text-align: left;
}