.invitation {
    background-color: #FFFFFF;
    padding-top: 20%;
    padding-bottom: 10%;
    text-align: center;
}

.pageTitle {
    font-size: 5vw;
    color: #848484;
    margin-bottom: 10%;
}

.content {
    font-family: "Nanum Myeongjo", serif !important;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    font-size: 4.5vw;
    line-height: 10vw;
    margin-bottom: 10vw;
}

.family {
    margin-top: 1vw;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;

    text-align: center;
    font-size: 4vw;
    line-height: 10vw;
}

.contact {
    height: auto;
    padding: 10vw 5vw;
    background-color: #FFFFFF;
}

.contactItem {
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;

    >div {
        margin: 0.5vw;
    }
}

.contactIcon {
    display: flex;
    flex-direction: row;
    width: 70%;
    justify-content: space-between;
}

.waveContainer {
    top: 0;
    left: 0;
    transform: translateY(20%);
    z-index: 1;
    width: 100%;
}