.Header {
    background-color: #F7F7F7;
    height: 100%;
    padding-top: 20%;
    padding-bottom: 20%;
}

.nameBox {
    width: 90%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin-top: 5vw;
}

.logo {
    margin-top: -3%;
    margin-right: -3%;
    width: 100%;
    display: flex;
    justify-content: flex-end;
}

.headerNameFont:last-child {
    margin-top: 1vw;
}

.headerFont {
    font-family: "Nanum Myeongjo", serif;
    color: rgb(77, 31, 3);
    line-height: 1.4em;
}

.headerNameFont {
    font-size: 6vw;
    letter-spacing: 0.25em;
    text-align: right;
}

.headerLocationFont {
    font-size: 4.5vw;
    letter-spacing: 0em;
    text-align: left;
}

.headerLocationDiv {
    display: flex;
    flex-direction: column;
    background-color: #F7F7F7;
    margin-top: 10%;
    margin-left: 10%;
}

.imgbox {
    margin-top: 15%;
    position: relative;
    max-width: 100vw;
    overflow: hidden;
    /* border-radius: 5px; */
}

.imgbox2 {
    margin-top: 15%;
    position: relative;
    max-width: 100vw;
    overflow: hidden;
    /* border-radius: 5px; */
}

