.Gallery {
    padding-top: 10%;
    padding-bottom: 10%;
    text-align: center;
}

.pageTitle {
    font-size: 5vw;
    color: #848484;
    margin-bottom: 10%;
}

.PhotoContainerColumn {
    overflow-y: auto;
    width: 100%;
    padding: 0vw 2vw;
    box-sizing: border-box;
}

.GridBoxColumn {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    /* 두 개의 행 */
    grid-auto-rows: auto;
    gap: 2vw;
    width: 100%;
}

.gridItemColumn {
    box-sizing: border-box;
    /* 이미지의 고정된 너비 */
    width: 100%;

    height: auto;
    /* 이미지의 고정된 높이 */
}

.gridItemColumn img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    /* 이미지가 아이템을 꽉 채우도록 설정 */
    display: block;
}

.PhotoContainerRow {
    overflow-x: auto;
    width: 100%;
    padding: 0vw 2vw;
    box-sizing: border-box;
}

.GridBoxRow {
    display: grid;
    grid-template-rows: repeat(2, 1fr);
    /* 두 개의 행 */
    grid-auto-flow: column;
    gap: 2vw;
    width: max-content;
}

.gridItemRow {
    box-sizing: border-box;
    /* 이미지의 고정된 너비 */
    width: 40vw;

    height: auto;
    /* 이미지의 고정된 높이 */
}

.gridItemRow img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    /* 이미지가 아이템을 꽉 채우도록 설정 */
    display: block;
}