.Calendar {
    padding-top: 10%;
    padding-bottom: 10%;
    text-align: center;
    background-color: #F7F7F7;
}

.pageTitle {
    font-size: 5vw;
    color: #848484;
    margin-bottom: 10%;
}

.ddaybox {
    margin-top: 2vw;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.ddayText {
    text-align: 'center';
    font-size: '3vw';
    line-height: '10vw';
}

.calendarContainer {
    width: 100%;
    border: none;
    font-family: "Nanum Myeongjo", serif;
    /* background-color: #fff; */
    background-color: #F7F7F7;
    padding: 0 10%;
}

.header {
    text-align: center;
    padding: 10px 0;
    font-weight: 600;
    font-size: 4vw;
    margin-bottom: 7vw;
}

.dayHeaderContainer {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    margin-bottom: 3vw;
}

.dayHeaderSun {
    text-align: center;
    padding: 1vw 0;
    font-weight: bold;
    font-size: 4vw;
    color: #D099A1;
}

.dayHeader {
    text-align: center;
    padding: 1vw 0;
    font-weight: bold;
    font-size: 4vw;
}

.dateGrid {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
}

.dateCellSun {
    text-align: center;
    padding: 3vw;
    font-size: 4vw;
    color: #D099A1;
    position: relative;
}

.dateCell {
    text-align: center;
    padding: 3vw;
    font-size: 4vw;
    color: #333;
    position: relative;
}

.emptyCell {
    padding: 10px 0;
}

/* Calendar.module.css */

.specialDateContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
    width: 11vw;
    height: 11vw;
}

.heartIcon {
    width: 100%;
    height: 100%;
}

.specialDateText {
    position: absolute;
    top: 30%;
    left: 50%;
    transform: translate(-50%, -30%);
    font-size: 3vw;
    color: white;
    font-weight: bold;
}

.timeText {
    font-size: 2vw;
    color: #B56576;
    margin-top: 1vw;
}

.waveContainer {
    top: 0;
    left: 0;
    transform: translateY(-70%);
    z-index: 1;
    width: 100%;
}